<template>
  <form @submit.prevent="Submit">
    <input v-model="text"
    >
    <button type="submit"> create todo</button>
  </form>
</template>

<script>
export default {
  data(){
    return{
      text: '  '
    }
  },
  methods:{
    Submit() {

      if (this.text.trim()) {
        let newTodo = {id: Date.now(), title: this.text, completed: false}
        this.$emit('addTodo', newTodo)
        this.text =''
      }
      this.text = this.text.trim()
    }

  }
}
</script>