<template>
    <li>
        <input type="checkbox" v-on:change="todo.completed = !todo.completed" v-if="todo.completed" checked>
        <input type="checkbox" v-on:change="todo.completed = !todo.completed" v-else >
        <span v-bind:class="{done: todo.completed}">
            <strong> {{index+1}}) </strong>
            {{todo.title | UpperCase}}
            
        </span>
        <button class="btn" 
            v-bind:class="{done: todo.completed}"
            v-on:click="$emit('remove-todo', todo.id)"
        > &times; </button>
    </li>
</template>

<script>
export default {

    props: {
        todo: {
            type: Object,
            required: true
        },
        index: Number
    },
    filters:{
        UpperCase(value){
            return value.toUpperCase()
        }
    }
    
}
</script>

<style scoped>

li{
    padding-top : 20px;
    display: flex;
    justify-content: space-between;
}
span.done {
    text-decoration: line-through;
}
.btn{
    background: red;
}

</style>